<template>
  <div class='welcome'>
    <div class="message">
      <p v-if="time<2"><span class="line1">또 다른 당신</span>과</p>
      <p v-if="time>2&&time<5"><span class="line2">은밀한 영화 이야기</span>를 나눠보세요!</p>
      <router-link to="/login" v-if="time>4" class="line3">❣️ 무비 클럽으로 입장 ❣️ </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'WelcomeView',
  data(){
    return {
      time: 0,
    }
  },

  // 1초에 1씩 time 증가
  mounted(){
    setInterval(()=>{
      this.time++;
    },1000)
  }
}
</script>

<style>
.welcome {
  background-image: url('@/assets/welcome.jpg');
  background-repeat: no-repeat;
  height: 100vh;
  margin: 0;
  padding: 0;
  background-size: cover;
}

.message{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: rgb(199, 199, 199);
  overflow: hidden; 
  font-size: 40px;
}
.line1{
  font-size: 70px;
  font-weight: bold;
  color:rgb(255, 196, 0);
}
.line2{
  font-size: 50px;
  font-weight: bold;
  color: rgb(213, 124, 255);
}
.line3{
  text-decoration: none;
  color: rgb(49, 255, 49);
  font-size: 50px;
}




</style>